// 13140121671  =>  "131****1671"
function MobilePhoneSecret(str) {
    if (null != str && str != undefined) {
        var pat = /(\d{3})\d*(\d{4})/;
        return str.replace(pat, '$1****$2');
    } else {
        return "";
    }
}

function CardnoSecret(str) {
    if (null != str && str != undefined) {
        var pat = /(\d{4})\d*(\d{3})/;
        return str.replace(pat, '$1 **** **** **** $2');
    } else {
        return "";
    }
}

export default {
    MobilePhoneSecret,
    CardnoSecret
}