import network from '../network'

// 认证提交公司信息
function uploadCompanyInfo(params) {
    let user = storage.get("user");
    params.user_id = user.user_id;
    return network.post('/company', params);
}

// 获取公司信息
function getCompanyInfo() {
    let user = storage.get("user");
    return network.get("/company", {
        params: {
            user_id: user.user_id
        }
    })
}

export default {
    uploadCompanyInfo,
    getCompanyInfo
}