<template>
  <div id="app">
    <div class="top-bg"></div>
    <div class="flex-cloum main">
      <span class="title margin-center">申请企业账号认证</span>
      <div class="flex-cloum content-box">
        <div class="flex-row-align-center item" v-for="(item,index) in dataArray" :key="index">
          <span class="item-title">{{item.item_title}}</span>
          <span class="item-value">{{item.item_value}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import companyApi from "network/api/company.js";
import utils from "utils/util.js";
export default {
  data() {
    return {
      titleArrays: [
        "企业名称",
        "社会信用代码",
        "对公账号",
        "开户行",
        "开户支行",
        "开户行地区",
        "企业联系人",
        "联系人电话",
        "邮箱",
        "公司地址"
      ],
      keyArrays: [
        "company_name",
        "company_code",
        "bank_card",
        "bank_name",
        "bank_name_sub",
        "bank_location",
        "contact_name",
        "contact_phone",
        "contact_email",
        "contact_address"
      ],
      dataArray: []
    };
  },
  //生命周期 - 创建完成（访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    this.requestVerifyData();
  },
  //方法
  methods: {
    // 获取基本信息
    requestVerifyData() {
      companyApi
        .getCompanyInfo()
        .then(res => {
          console.log(res);
          if (res.data && res.data != "") {
            let data = res.data;
            // 表示有公司信息
            let arrays = [];
            for (let i = 0; i < this.keyArrays.length; i++) {
              let keyValue = this.keyArrays[i];
              let titleValue = this.titleArrays[i];
              let infoValue = data[keyValue];
              if (keyValue == "bank_card") {
                infoValue = utils.CardnoSecret(infoValue);
              }
              if (keyValue == "contact_phone") {
                infoValue = utils.MobilePhoneSecret(infoValue);
              }

              let obj = {
                item_title: titleValue,
                item_value: infoValue
              };
              arrays.push(obj);
            }
            console.error(arrays);
            this.dataArray = arrays;
          }
        })
        .catch(res => {
          console.error(res);
        });
    }
  },
  //引入的组件
  components: {}
};
</script>
